import { device } from "@layouts/styleConfig";
import React from "react";
import styled from "styled-components";

type LoadingLayoutType = {
  size?: number;
};

const LoadingLayout = styled.div.withConfig({
  componentId: "LogoLayout",
})<LoadingLayoutType>`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s ease-in, color 0.2s ease-in;
  background: ${({ theme }) => theme.bgColor};
  transform: ${({ size }) => (size ? `scale(${size})` : `scale(0.6)`)};

  :hover {
    .loading_logo {
      animation: spinning 2s 1;
    }
  }

  .loading_logo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    box-sizing: border-box;
    border: 5px solid ${({ theme }) => theme.color};

    animation-fill-mode: both;
  }

  span {
    color: ${({ theme }) => theme.color};
    width: 50%;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: 6.5rem;
    font-weight: 600;

    :first-child {
      text-indent: 10px;
    }
    :last-child {
      text-indent: -10px;
      transform: rotate(-30deg);
    }
  }

  @keyframes spinning {
    0% {
      transform: rotateZ(0);
    }
    100% {
      transform: rotateY(360deg);
    }
  }

  @media ${device.tablet} {
    transform: ${({ size }) => (size ? `scale(${size})` : `scale(1.1)`)};
  }
`;

export default function Logo({ size }: LoadingLayoutType) {
  return (
    <LoadingLayout size={size}>
      <div className="loading_logo">
        <span>D</span>
        <span>C</span>
      </div>
    </LoadingLayout>
  );
}
