import React from "react";
import styled from "styled-components";
import { Link as I18nLink, useTranslation } from "gatsby-plugin-react-i18next";
import { FaUserCircle, FaLaptop, FaBookOpen } from "react-icons/fa";
import { device } from "@layouts/styleConfig";

type LayoutType = {
  type?: string;
};
const Layout = styled.div.withConfig({
  componentId: "HeroCard_Layout",
})<LayoutType>`
  a {
    position: relative;
    text-decoration: none;
    /* width: fit-content; */
    border-bottom: 2px solid ${({ theme }) => theme.postColor};
    padding: 6px 4px;
    /* border-radius: 8px; */
    display: flex;
    overflow: hidden;
    margin-bottom: 40px;
    transition: all 0.1s linear;

    ${({ type }) =>
      type === "primary"
        ? ` 
            flex-direction: column;
            align-items: center;
          `
        : `
            flex-direction: row;
          `};

    .avatar {
      width: 4rem;
      height: 4rem;

      color: ${({ theme }) => theme.postColor};
      > svg {
        flex-grow: 1;
        width: 100%;
        height: 100%;
      }
    }

    .card-content {
      /* flex-grow: 1; */
      display: flex;
      flex-direction: column;
      justify-content: center;
      color: ${({ theme }) => theme.postColor};
      max-width: 150px;
      padding: 0 4px;
      word-break: break-all;
      line-height: 150%;
      margin-left: 8px;
      ${({ type }) =>
        type === "primary"
          ? ` 
              text-align: center;
              max-width: 200px;
              margin-left: 0;
              text-align: center;
              margin-top: 8px;

              p {
                &.description {
                  margin-top: 0px;
                }
              }
            `
          : `
              text-align: left;
            `};

      p {
        margin: 0;
        &.title {
          font-size: 1.25rem;
          font-weight: 600;
          ::first-letter {
            text-transform: uppercase;
          }
        }

        &.description {
          font-size: 0.75rem;
          margin-top: 8px;
          flex-grow: 1;
        }
      }
    }

    .hover-hint {
      transition: all 0.1s ease-in-out;

      position: absolute;
      left: 0;
      border-radius: 6px;
      width: 100%;
      height: 100%;

      background: ${({ theme }) => theme.color};
      opacity: 0.8;
      color: ${({ theme }) => theme.bgColor};
      text-align: center;
      justify-content: center;
      align-items: center;
      padding: 8px;
      box-sizing: border-box;
      top: -100%;
    }

    :hover {
      /* box-shadow: 0px 0px 3px red; */
      filter: ${({ theme }) =>
        `drop-shadow(5px 5px 1px ${theme.overlayColor} );`};

      border-color: ${({ theme }) => theme.color};
      .avatar,
      .card-content {
        color: ${({ theme }) => theme.color};
      }

      transform: translate(-5px, -5px);
      .hover-hint {
        display: flex;
        top: 0%;
      }
    }
  }

  @media ${device.mobile_maxHeight_667} {
    a {
      .avatar {
        width: 2.5rem;
        height: 2.5rem;
      }
    }
  }

  @media ${device.tablet} {
    a {
      .avatar {
        width: 5rem;
        height: 5rem;
      }
    }
  }
`;

type HeroCardPropTypes = {
  title: "resume" | "project" | "blog";
  description?: string;
  hoverHint?: string;
  link: string;
  type?: "primary" | "secondary";
  imgSrc?: string;
};

const imgDictionary = {
  resume: <FaUserCircle />,
  blog: <FaBookOpen />,
  project: <FaLaptop />,
};

export default function HeroCard(props: HeroCardPropTypes) {
  const {
    title,
    description,
    hoverHint,
    link,
    type = "primary",
    imgSrc,
  } = props;

  const { t } = useTranslation();

  return (
    <Layout className={title} type={type}>
      <I18nLink to={link}>
        {imgSrc ? (
          <img className="avatar" src={imgSrc} />
        ) : (
          <div className="avatar">{imgDictionary[title]}</div>
        )}
        <div className="card-content">
          {title && <p className="title">{t(title)}</p>}
          {description && <p className="description">{description}</p>}
        </div>
        {hoverHint && <div className="hover-hint">{hoverHint}</div>}
      </I18nLink>
    </Layout>
  );
}
