import React, { useEffect, useRef } from "react";
import styled from "styled-components";
import { gsap } from "gsap";
import { device } from "@layouts/styleConfig";
import HeroCard from "@components/HeroCard";
import { useTranslation } from "gatsby-plugin-react-i18next";
import Logo from "@components/Logo";

const Layout = styled.div.withConfig({ componentId: "IndexPage_Layout" })`
  position: relative;
  height: calc(100vh - 40px);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;

  .logo {
    box-sizing: border-box;
    margin: 80px 0 10px;
  }

  .skip {
    position: absolute;
    top: 10px;
    left: 10px;
    color: ${({ theme }) => theme.frontPageContent};
    transform: rotate(-3deg);
    border: 1px solid ${({ theme }) => theme.frontPageContent};
    padding: 4px;
    font-weight: 800;
    border-radius: 4px;

    :hover {
      cursor: pointer;
    }
  }

  .board {
    position: relative;
    transform: perspective(136px) rotateX(25deg);
    transform-origin: 50% 0%;
    overflow: hidden;
    height: 50vh;
    width: 100%;
  }
  .content {
    width: 100%;
    text-align: center;
    white-space: pre;
    position: absolute;
    top: 25vh;
    line-height: 185%;
    font-size: 0.625rem;
    color: ${({ theme }) => theme.frontPageContent};
  }

  .menu {
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    opacity: 0;
  }

  @media ${device.mobile_maxHeight_667} {
    .logo {
      margin: 40px 0 10px;
    }
    .menu {
      margin-top: 50px;
    }
    .content {
      line-height: 155%;
    }
  }

  @media ${device.tablet} {
    height: calc(100vh - 70px);
    padding: 0 32px;

    .board {
      transform: perspective(186px) rotateX(25deg);
    }
    .skip {
      transform: scale(1.5);
      left: 30px;
      top: 30px;
    }
    .content {
      line-height: 130%;
      font-size: 1rem;
      letter-spacing: 0.25rem;
      font-weight: 800;
      top: 30vh;
    }
    .menu {
      display: flex;
      flex-direction: row;
      width: 100%;
      justify-content: space-evenly;
      margin-top: 150px;
    }
  }
`;

export default function IndexPage() {
  const { t } = useTranslation();
  let tl = gsap.timeline();
  let tl_skip = gsap.timeline();
  const ref = useRef<HTMLDivElement>(null);
  useEffect(() => {
    const component = ref.current;
    if (!component) return;

    tl_skip.fromTo(
      component.querySelector(".skip"),
      { rotate: -3 },
      {
        rotate: 3,
        repeat: -1,
        duration: 1.5,
        yoyo: true,
        ease: "linear",
      }
    );

    tl.to(
      component.querySelector(".content"),

      {
        top: "-30vh",
        duration: 30,
        onComplete: stopSkipAnimation,
        ease: "linear",
      }
    )
      .to(component.querySelector(".skip"), {
        opacity: 0,
        display: "none",
      })
      .fromTo(
        component.querySelector(".content"),
        { opacity: 1 },
        { opacity: 0 }
      )
      .fromTo(
        component.querySelector(".board"),
        { display: "block" },
        { display: "none" }
      )
      .fromTo(
        component.querySelector(".menu"),
        { display: "none", opacity: 0 },
        { display: "flex", opacity: 1, y: -50 }
      )
      .fromTo(
        component.querySelectorAll(".menu > div"),
        {
          opacity: 0,
        },
        {
          opacity: 1,
          y: 50,
          stagger: 0.5,
        }
      );
  }, [ref]);

  function stopSkipAnimation() {
    tl_skip.pause();
  }

  return (
    <Layout ref={ref}>
      <div className="logo">
        <Logo />
      </div>
      <div
        className="skip"
        onClick={() => {
          tl.seek(30);
          stopSkipAnimation();
        }}
      >
        {t("frontpage:skip")}
      </div>
      <div className="board">
        <div className="content">{t("frontpage:content")}</div>
      </div>
      <div className="menu">
        <HeroCard type="secondary" link="/resume" title="resume" />
        <HeroCard type="secondary" link="/blog" title="blog" />
        <HeroCard type="secondary" link="/project" title="project" />
      </div>
    </Layout>
  );
}
